<template>

    <div class="index-page">
        <div class="container-fluid">
            <div class="row">
                
                <div class="section-2 mt-3">
                    <div class="row">
                        <button   class="col-lg-3 col-md-4 col-sm-6 col-12  mar-bot" v-for="(item,index) in dishes" :key="index">
                            <div  @click="IsDishes=('qwertyy'+item.id);FoodsList(list=dishes,index);toggleActive(index)" 
                            :class="{ active: item.active}" class="eat-card "
                           :id="'abs'+item.id"
                             
                            >
                                <div class="first">
                                    <img src="../assets/images/png/eat1.png" alt="" class="eat-img">
                                    <div class="eat-name">{{item.title}}</div>
                                    <div class="eat-info"><span>{{item.price}}</span><span>
                                        <img src="../assets/images/icons/clock.svg" alt=""> 15м</span>
                                    </div>
                                </div>
                                    <div class="second">
                                        <div class="title">Хотите узнать подробнее 0 salad 20</div>
                                        
                                    <router-link :to="'/about/'+item.id" class="more">Да мне интресно</router-link>
                                    <button @click="active=false" class="more">Не интресно</button>            
                                </div>
                                 <div class="checking">
                                    <img  src="../assets/images/icons/check.svg" alt="">
                                </div>
                            </div>
                        </button>
                    </div>
                </div>
           </div>
        </div>
     </div>
  </template>
  
  <script>
   
   import {foods} from '../store/foodslist.js'
    import axios from 'axios'
    import {API} from '../store/config.js'
    export default{
      components:{
      },
      data(){
          return{
          data:'',
          dishes:'',
          IsDishes:'',
          isActive: false,
          active_el:0
          }
      },
      mounted() {
        const token = localStorage.getItem('D-Token')
                axios 
                .get (API+'category/'+6+'/dishes',
                {
                    headers: {
                    Authorization: `Bearer ${token}`
                  }

                }
                )
                .then(res=>{
                    this.dishes=res.data.data
                })
      },
      methods: {
        FoodsList(list,index){
            foods.push({
                    food : list[index],
                    count : 1
                })
                   this.$store.dispatch("foodslist",foods)
            },
            toggleActive(index) {
            let item = this.dishes[index];
            item.active = !item.active;
            this.$set(this.dishes, index, item);

            }
      },
     }
  </script>
  